import * as Three from 'three';

class MarkerBox extends Three.LineSegments {
  constructor(markers) {
    const geometry = new Three.EdgesGeometry(
      MarkerBox.generateMarkerGeometry(markers)
    );
    const material = new Three.LineBasicMaterial({
      color: 0xc0c0c0,
      linewidth: 1
    });
    super(geometry, material);

    this.geometry = geometry;
    this.material = material;
  }

  static generateMarkerGeometry(markers) {
    const geometry = new Three.Geometry();
    const markerWidth = 0.99;

    if (!markers || Object.keys(markers).length === 0) {
      return geometry;
    }

    let px = 0;
    let py = 0;
    let pz = 0;

    // markers.forEach((m, index, array) => {
    markers.forEach((m, index) => {
      const cx = m.position.x + m.offset.x;
      const cy = m.position.z + m.offset.z;
      const cz = m.position.y + m.offset.y;

      if (index > 0) {
        const sx = cx - px;
        const sy = cy - py;
        const sz = cz - pz;
        const rad = Math.atan2(sx, sz);
        const markerDepth = Math.sqrt(sx * sx + sz * sz);
        const geom = new Three.BoxGeometry(
          markerWidth,
          markerWidth,
          markerDepth
        );
        const mesh = new Three.Mesh(geom);
        mesh.position.set(px + sx / 2, py + sy / 2, pz + sz / 2);
        mesh.rotateY(rad);
        geometry.mergeMesh(mesh);
      }
      px = cx;
      py = cy;
      pz = cz;
    });
    return geometry;
  }
}

export default MarkerBox;
