import React from 'react';
import { useTheme } from '@material-ui/core/styles';
import { Paper, Box } from '@material-ui/core';
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  Label,
  ResponsiveContainer
} from 'recharts';
import Title from 'src/components/Title';

// Generate Graph Data
function createData(time, amount) {
  return { time, amount };
}

const data = [
  createData('MON', 1),
  createData('TUE', 4),
  createData('WED', 5),
  createData('THU', 2),
  createData('FRI', 3),
  createData('SAT', 3),
  createData('SUN', 6)
  //   createData("08:00", 2400),
  //   createData("09:00", undefined),S
];

// const useStyles = makeStyles((theme) => ({
//   fixedHeightMed: {
//     height: 370
//   },
//   fixedHeightMini: {
//     height: 292
//   }
// }));

export default function Chart() {
  const theme = useTheme();

  return (
    <React.Fragment>
      <Paper>
        <Box height={500}>
          <Title>Average concret block per day</Title>
          <ResponsiveContainer>
            <LineChart
              data={data}
              margin={{
                top: 16,
                right: 16,
                bottom: 0,
                left: 24
              }}
            >
              <XAxis dataKey="time" stroke={theme.palette.text.secondary} />
              <YAxis stroke={theme.palette.text.secondary}>
                <Label
                  angle={270}
                  position="left"
                  style={{
                    textAnchor: 'middle',
                    fill: theme.palette.text.primary
                  }}
                >
                  Block Amount
                </Label>
              </YAxis>
              <Line
                type="monotone"
                dataKey="amount"
                stroke={theme.palette.primary.main}
                dot={false}
              />
            </LineChart>
          </ResponsiveContainer>
        </Box>
      </Paper>
    </React.Fragment>
  );
}
