import * as Three from 'three';

class PlanBox extends Three.LineSegments {
  constructor(markers) {
    const geometry = PlanBox.generatePlanGeometry(markers);
    const material = new Three.LineBasicMaterial({
      color: 0x0040c0,
      linewidth: 1
    });
    super(geometry, material);

    this.geometry = geometry;
    this.material = material;
  }

  static generatePlanGeometry(plans) {
    const geometry = new Three.Geometry();

    if (!plans || Object.keys(plans).length === 0) {
      return geometry;
    }

    let px1 = 0;
    let py1 = 0;
    let pz1 = 0;
    let px2 = 0;
    let py2 = 0;
    let pz2 = 0;
    const planSize = 1;

    plans.forEach((m, index, array) => {
      let cx;
      let cy;
      let cz;
      let ox;
      let oy;
      let oz;

      if (m.length > 3) {
        cx = Number(m[1]);
        cy = Number(m[3]);
        cz = Number(m[2]);
        ox = Number(m[4]);
        oy = Number(m[6]);
        oz = Number(m[5]);
      } else {
        cx = Number(m[0]);
        cy = Number(m[2]);
        cz = Number(m[1]);
        ox = 0;
        oy = 0;
        oz = 0;
      }

      const cx1 = cx + ox - planSize / 2;
      const cy1 = cy + oy - planSize / 2;
      const cz1 = cz + oz;
      const cx2 = cx + ox + planSize / 2;
      const cy2 = cy + oy + planSize / 2;
      const cz2 = cz + oz;

      if (index === 0) {
        geometry.vertices.push(new Three.Vector3(cx1, cy1, cz1));
        geometry.vertices.push(new Three.Vector3(cx1, cy2, cz1));
        geometry.vertices.push(new Three.Vector3(cx1, cy2, cz1));
        geometry.vertices.push(new Three.Vector3(cx2, cy2, cz2));
        geometry.vertices.push(new Three.Vector3(cx2, cy2, cz2));
        geometry.vertices.push(new Three.Vector3(cx2, cy1, cz2));
        geometry.vertices.push(new Three.Vector3(cx2, cy1, cz2));
        geometry.vertices.push(new Three.Vector3(cx1, cy1, cz1));
      } else {
        geometry.vertices.push(new Three.Vector3(px1, py1, pz1));
        geometry.vertices.push(new Three.Vector3(cx1, cy1, cz1));
        geometry.vertices.push(new Three.Vector3(px1, py2, pz1));
        geometry.vertices.push(new Three.Vector3(cx1, cy2, cz1));
        geometry.vertices.push(new Three.Vector3(px2, py2, pz2));
        geometry.vertices.push(new Three.Vector3(cx2, cy2, cz2));
        geometry.vertices.push(new Three.Vector3(px2, py1, pz2));
        geometry.vertices.push(new Three.Vector3(cx2, cy1, cz2));
      }

      if (index === array.length - 1) {
        geometry.vertices.push(new Three.Vector3(cx1, cy1, cz1));
        geometry.vertices.push(new Three.Vector3(cx1, cy2, cz1));
        geometry.vertices.push(new Three.Vector3(cx1, cy2, cz1));
        geometry.vertices.push(new Three.Vector3(cx2, cy2, cz2));
        geometry.vertices.push(new Three.Vector3(cx2, cy2, cz2));
        geometry.vertices.push(new Three.Vector3(cx2, cy1, cz2));
        geometry.vertices.push(new Three.Vector3(cx2, cy1, cz2));
        geometry.vertices.push(new Three.Vector3(cx1, cy1, cz1));
      }

      px1 = cx1;
      py1 = cy1;
      pz1 = cz1;
      px2 = cx2;
      py2 = cy2;
      pz2 = cz2;
    });

    return geometry;
  }
}

export default PlanBox;
