import React from 'react';
import { Box, makeStyles, Paper } from '@material-ui/core';
import Title from 'src/components/Title';
import VerticalSlider from './VerticalSlider';
import HorizontalSlider from './HorizontalSlider';
import KonvaCycle from './KonvaCycle';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingBottom: theme.spacing(3),
    paddingTop: theme.spacing(3)
  }
}));

const AttitudeIndicator = (props) => {
  const classes = useStyles();
  const { title, yaw, pitch, roll } = props;

  return (
    <>
      <Box display="flex" justifyContent="center">
        <Title>{title}</Title>
      </Box>
      <Paper>
        <HorizontalSlider degreeValue={roll} sliderLabel={'Rolling'} />
        <div style={{ display: 'flex' }}>
          <VerticalSlider degreeValue={pitch} sliderLabel={'Pitching'} />
          <div style={{ marginTop: 20, marginLeft: -40 }}>
            <KonvaCycle
              title="Front Attitude Indicator"
              yaw={yaw}
              pitch={pitch}
              roll={roll}
            />
          </div>
        </div>
        <HorizontalSlider degreeValue={yaw} sliderLabel={'Yawing'} />
      </Paper>
    </>
  );
};

export default AttitudeIndicator;
