import React from 'react';
import PropTypes from 'prop-types';
import { Stage, Layer, Line, Circle, Arc } from 'react-konva';
import Box from '@material-ui/core/Box';

function radian_to_digree(rad) {
  return rad * (180 / Math.PI);
}

class KonvaCycle extends React.Component {
  constructor(props) {
    super(props);

    this.width = props.width;
    this.height = props.height;

    this.yaw = props.yaw;
    this.pitch = props.pitch;
    this.roll = props.roll;
  }

  componentDidMount() {}

  componentWillUnmount() {}

  meterComponent(param) {
    return (
      <Layer x={param.x} y={param.y}>
        <Circle radius={param.meterRadius} fill="#92dce5" stroke="#7c7c7c" />
        <Arc
          outerRadius={param.meterRadius}
          angle={180}
          rotation={-param.roll}
          fill="#eee5e9"
          stroke="#7c7c7c"
          strokeWidth={2}
        />
        <Line
          points={[-param.meterRadius, 0, param.meterRadius, 0]}
          stroke="black"
          rotation={-param.roll}
          strokeWidth={1}
        />
        <Line
          points={[0, -param.meterRadius, 0, param.meterRadius]}
          stroke="black"
          rotation={-param.roll}
          strokeWidth={1}
        />
        <Line
          points={[
            -param.meterRadius,
            -param.cursorPitch * param.meterRadius,
            param.meterRadius,
            -param.cursorPitch * param.meterRadius
          ]}
          stroke="#D64933"
          rotation={-param.roll}
          strokeWidth={1.5}
        />
        <Line
          points={[
            -param.cursorYaw * param.meterRadius,
            -param.meterRadius,
            -param.cursorYaw * param.meterRadius,
            param.meterRadius
          ]}
          stroke="#D64933"
          rotation={-param.roll}
          strokeWidth={1.5}
        />
      </Layer>
    );
  }

  render() {
    const innerSize = this.width / 5;
    const outerSize = this.width / 4;
    const defaultProps = {
      bgcolor: 'background.paper',
      m: 1,
      border: 1,
      style: { width: '5rem', height: '2rem' }
    };

    let yaw = 0;
    let roll = 0;
    let pitch = 0;
    /*if (this.props.yaw && this.props.pitch && this.props.roll) {
      yaw = radian_to_digree(this.props.value.front.yaw);
      roll = radian_to_digree(this.props.value.front.roll);
      pitch = radian_to_digree(this.props.value.front.pitch);
    }*/
    console.log(this.props.yaw);
    if (this.props.yaw && this.props.pitch && this.props.roll) {
      yaw = this.props.yaw;
      roll = this.props.roll;
      pitch = this.props.pitch;
    }

    return (
      <>
        <Box margin="auto">
          <Stage width={this.width} height={this.height}>
            {this.meterComponent({
              x: outerSize * 2,
              y: this.height / 2,
              labelWidth: outerSize,
              meterRadius: innerSize,
              yaw,
              pitch,
              roll,
              cursorYaw: yaw / 30,
              cursorPitch: pitch / 30
            })}
          </Stage>
          {/* <Box display="flex" justifyContent="center">
            <Box borderColor="grey.500" {...defaultProps}>
              <Subtitle>yaw: {yaw}</Subtitle>
            </Box>
            <Box borderColor="grey.500" {...defaultProps}>
              <Subtitle>pitch: {pitch}</Subtitle>
            </Box>
            <Box borderColor="grey.500" {...defaultProps}>
              <Subtitle>roll: {roll}</Subtitle>
            </Box>
          </Box>*/}
        </Box>
      </>
    );
  }
}

KonvaCycle.propTypes = {
  width: PropTypes.number,
  height: PropTypes.number,
  title: PropTypes.string
};

KonvaCycle.defaultProps = {
  width: 330,
  height: 280,
  title: 'Attitude Indicator'
};

export default KonvaCycle;
