/* eslint-disable react/destructuring-assignment */
import React from 'react';
import PropTypes from 'prop-types';
import * as Three from 'three';
import Box from '@material-ui/core/Box';
import MeterBox from './MapView/MeterBox';
import MarkerBox from './MapView/MarkerBox';
import PlanBox from './MapView/PlanBox';
import Title from 'src/components/Title';

const OrbitControls = require('three-orbit-controls')(Three);

class MapView extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
    this.width = props.width;
    this.height = props.height;
  }

  componentDidMount() {
    this.scene = new Three.Scene();
    this.camera = new Three.PerspectiveCamera(
      75,
      this.width / this.height,
      0.1,
      1000
    );
    this.camera.position.set(-0.8, 1.5, -1.5);
    this.camera.lookAt(new Three.Vector3(0, 0, 0));

    this.renderer = new Three.WebGLRenderer({ antialias: true, alpha: true });
    this.renderer.setSize(this.width, this.height);
    this.renderer.setClearColor(0xffffff);
    // this.renderer.autoClear = false;
    // this.renderer.setClearColor(0x00000000, 0.0);

    this.mount.appendChild(this.renderer.domElement);
    this.controls = new OrbitControls(this.camera, this.renderer.domElement);

    // this.scene.add(new GridBox());
    this.grid = new Three.GridHelper(100, 200, 0xdfaf8f, 0xe8e8e8);
    this.grid.position.y -= 0.6;
    this.scene.add(this.grid);
    // this.scene.fog = new Three.Fog(0x808080, 50, 2000);

    this.boxFront = new MeterBox();
    this.boxFront.rotation.z = 0.0;
    this.boxFront.position.set(0, 0, 0);
    this.scene.add(this.boxFront);

    this.boxRear = new MeterBox();
    this.boxRear.rotation.z = 0.0;
    this.boxRear.position.set(0, 0, 0);
    this.scene.add(this.boxRear);

    this.animate = this.animate.bind(this);
    this.start();
  }

  componentWillUnmount() {
    this.stop();
    this.mount.removeChild(this.renderer.domElement);
  }

  start() {
    if (!this.frameId) {
      this.frameId = window.requestAnimationFrame(this.animate);
    }
  }

  stop() {
    cancelAnimationFrame(this.frameId);
  }

  animate() {
    this.renderScene();
    this.frameId = window.requestAnimationFrame(this.animate);
  }

  // eslint-disable-next-line react/sort-comp
  renderScene() {
    this.update();
    this.controls.update();
    this.renderer.render(this.scene, this.camera);
  }

  update() {
    // eslint-disable-next-line react/destructuring-assignment
    if (this.props.value && this.props.value.front) {
      const { center } = this.props.value.front;
      const { len } = this.props.value.front;
      const { yaw } = this.props.value.front;
      const { roll } = this.props.value.front;
      const { pitch } = this.props.value.front;
      this.boxFront.position.set(center.x, center.z, center.y);
      this.boxFront.scale.set(len, len, len);
      this.boxFront.rotation.set(pitch, yaw, roll, 'YXZ');
    }

    // eslint-disable-next-line react/destructuring-assignment
    if (this.props.value && this.props.value.rear) {
      const { center } = this.props.value.rear;
      const { len } = this.props.value.rear;
      const { yaw } = this.props.value.rear;
      const { roll } = this.props.value.rear;
      const { pitch } = this.props.value.rear;
      this.boxRear.position.set(center.x, center.z, center.y);
      this.boxRear.scale.set(len, len, len);
      this.boxRear.rotation.set(pitch, yaw, roll, 'YXZ');
    }

    if (this.markerBox) {
      this.scene.remove(this.markerBox);
      this.markerBox = null;
    }

    if (this.props.showMarker) {
      this.markerBox = new MarkerBox(this.props.marker);
      this.markerBox.rotation.z = 0;
      this.markerBox.position.set(0, 0, 0);
      this.scene.add(this.markerBox);
    }

    if (this.planBox) {
      this.scene.remove(this.planBox);
      this.planBox = null;
    }
    this.planBox = new PlanBox(this.props.plan);
    this.planBox.rotation.z = 0;
    this.planBox.position.set(0, 0, 0);
    this.scene.add(this.planBox);
  }

  render() {
    return (
      <>
        <Box display="flex" justifyContent="center">
          <Title>3D Map</Title>
        </Box>
        <Box margin="auto">
          <div
            // eslint-disable-next-line arrow-parens
            ref={(mount) => {
              this.mount = mount;
            }}
          />
        </Box>
        {/* </ResponsiveContainer> */}
      </>
    );
  }
}

MapView.propTypes = {
  width: PropTypes.number,
  height: PropTypes.number,
  value: PropTypes.object,
  plan: PropTypes.object,
  marker: PropTypes.object,
  showMarker: PropTypes.bool
};

MapView.defaultProps = {
  width: 749,
  height: 520,
  value: {},
  plan: {},
  marker: {},
  showMarker: false
};

export default MapView;
